import { useEffect } from 'react';
import { Toaster } from 'react-hot-toast';
import { useDispatch, useSelector } from 'react-redux';
import { Redirect, Route, Switch } from 'react-router';
import { Spinner } from './Components/Spinner/Spinner';
import Auth from './Pages/Auth/Auth';
import { GetRoleId } from './Redux/Auth/AuthSelector';
import { loadAdminThunk, loadOperatorThunk } from './Redux/Auth/AuthThunks';
import { AdminRoutes, OperatorRoutes } from './routes';


export default function App() {
  const dispatch = useDispatch();
  const role_id = useSelector(GetRoleId);
  
  useEffect(() => {
    dispatch(loadOperatorThunk());
    dispatch(loadAdminThunk());
  }, [dispatch]);

    return (
      <>
          <div className="App">
            {
              (role_id === 1)
              ?
                <Switch>
                  {AdminRoutes.map(({path, Component}) => <Route component={Component} exact key={path} path={path} />)}
                  <Redirect to='/operators'/>
                </Switch>
              : (role_id === 2)
                ?
                  <Switch>
                    {OperatorRoutes.map(({path, Component}) => <Route component={Component} exact key={path} path={path} />)}
                    <Redirect to='/unapproved'/>
                  </Switch>
                : (role_id === 0)
                  ?
                    <>
                      <Route path='/auth' component={Auth} />
                      <Redirect to='/auth' />
                    </>
                  : <>
                      <Spinner />
                    </>

            }
            <Toaster />
          </div>
      </>
          )
}