import { useState } from 'react';
import styles from './Slider.module.css';
import rightArrow from '../../Images/right-arrow.png';

const imageArray = [
    'data:image/jpeg;base64,',
    'data:image/jpeg;base64,',
    'data:image/jpeg;base64,',
    'data:image/jpeg;base64,',
    'data:image/jpeg;base64,',
    'data:image/jpeg;base64,',
    'data:image/jpeg;base64,',
    'data:image/jpeg;base64,',
    'data:image/jpeg;base64,',
    'data:image/jpeg;base64,',
    'data:image/jpeg;base64,',
    'data:image/jpeg;base64,',
];
// (image_number === imageArray.length)
export const Slider = () => {
    const [image_number, setImage_number] = useState(0);
    return (
        <div className={styles.main_block}>
            <div className={`${styles.image_block} relative top-0 left-0`}>
                <button disabled={(image_number === 0)} className={`${styles.arrow_image} ${styles.arrow_image_left} transform rotate-180`} onClick={() => setImage_number(image_number - 1)}>
                    <img alt='Left arrow' src={rightArrow}/>
                </button>
                <img alt='Main' className={styles.main_image} src={imageArray[image_number]}/>
                <button className={`${styles.arrow_image} ${styles.arrow_image_right}`} disabled={(image_number === (imageArray.length - 1))} onClick={() => setImage_number(image_number + 1)}>
                    <img alt='Right arrow' src={rightArrow} />
                </button>
            </div>
            <p className='text-center text-white'>{image_number + 1} / {imageArray.length}</p>
        </div>
    );
}