import { api } from '../../../api';
import { OperatorsActions } from "./OperatorsReducer";


export const getOperatorsThunk = (token: string, page: number) => {
    return async (dispatch: any) => {
        try {
            if (token === "" || page === null) return;
            dispatch(OperatorsActions.changeIsLoading(true));
            const response = await api.getApi({url: `admin/get-all-operators?page=${page}&limit=10`, token: token, withCredentials: true, contentType: false});
            dispatch(OperatorsActions.changeOperatorsCound(response.data.count));
            dispatch(OperatorsActions.addOperators(response.data.operators));
            dispatch(OperatorsActions.changeIsLoading(false));
        } catch (error: any) {
            console.log(error.response.message);
        }
    };
};


export const deleteOperatorThunk = (id: any, token: string) => {
    return async (dispatch: any) => {
        try {
            if (token === "") return;
            await api.postApi({url: `admin/delete-operator/${id}`, token: token, withCredentials: true, params: {}});
        } catch (error: any) {
            console.log(error.response.message);
        }
    };
};


export const updateOperatorThunk = (operatorData: {name: string, email: string, phone: number}, id: number, token: string) => {
    debugger;
    return async (dispatch: any) => {
        try {
            if (token === "") return;
            await api.postApi({url: `admin/update-operator/${id}`, token: token, withCredentials: true, params: {full_name: operatorData.name, email: operatorData.email, phone: operatorData.phone}});
        } catch (error: any) {
            console.log(error.response.message);
        }
    };
};

export const searchOperatorThunk = (name: string, phone: string, token: string) => {
    return async (dispatch: any) => {
        try {
            if (token === "") return;
            const response = await api.getApi({url: `admin/get-all-operators?full_name=${name}&phone=${phone}`, token: token, withCredentials: true, contentType: false});
            dispatch(OperatorsActions.addSerchedOperators(response.data.operators));
            if (name === '' && phone === '') dispatch(OperatorsActions.makeSearchOperatorsNull())
        } catch (error: any) {
            console.log(error.response.message);
        }
    };
};

export const addOperatorThunk = (operatorData: {name: string, email: string, phone: number, password: string}, token: string) => {
    return async (dispatch: any) => {
        try {
            if (token === "") return;
            await api.postApi({url: `admin/add-operator`, token: token, withCredentials: true, params: {full_name: operatorData.name, email: operatorData.email, phone: operatorData.phone, password: operatorData.password}});
        } catch (error: any) {
            console.log(error.response.message);
        }
    };
};