import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { ChangeImagesModal } from "../../../Components/Modal/ChangeImagesModal/ChangeImagesModal";
import { Sidebar } from "../../../Components/Sidebar/Sidebar";
import { GetImagesLocations } from "../../../Redux/Admin/Pictures/ImagesSelector";
import { getImagesLocationsThunk } from "../../../Redux/Admin/Pictures/ImagesThunk";
import { GetAccessToken } from "../../../Redux/Auth/AuthSelector";
import styles from './Images.module.css';

export const Pictures = () => {
    
    const access_token = useSelector(GetAccessToken);
    const dispatch = useDispatch();
    // State
    const imagesLocations = useSelector(GetImagesLocations);
    const [clicked_id, setClicked_id] = useState(0);
    const [change_image_modal_shown, setChange_image_modal_shown] = useState(false);
    
    let imagesLocationsRender;
    if (imagesLocations) {
        imagesLocationsRender = imagesLocations.map((imagesLocation: {id: number; place: string}) => {
            return (
                <div
                    onClick={() => {setChange_image_modal_shown(true); setClicked_id(imagesLocation.id)}}>
                        {imagesLocation.place}
                </div>
            );
        })
    }

    useEffect(() => {
        dispatch(getImagesLocationsThunk(access_token));
    }, [dispatch, access_token])

    return (
        <div className={styles.main_block}>
            <Sidebar />
            <div className={styles.content_block}>
                {imagesLocations
                    ? (imagesLocationsRender)
                    : null
                }
                {(clicked_id !== 0)
                    ? <ChangeImagesModal 
                    isModalOpen={change_image_modal_shown} 
                    changeModalOpen={setChange_image_modal_shown}
                    id={clicked_id}
                />
                    : console.log('Hello')
                }
                
            </div>
        </div>
    );
}