import { InferActionsTypes } from '../../store';


const SpecificationsInitialState = {
    specificationsData: [] as Array<specificationsDataType>,
    searchSpecifications: null as any,
    specificationData: {} as specificationDataType,
    enabled_values: [] as Array<valuesType>,
    disabled_values: [] as Array<valuesType>,
    isLoading: false,
    totalCount: null 
};

type valuesType = [
    {
        id: number;
        absolute_value: string;
        enable: boolean;
        value_translations: null | {
            language_id: number;
            name: string;
        }
    }
];

export type specificationsDataType = {
    id: number;
    absolute_name: string;
    is_multiple: boolean;
    is_required: boolean;
    is_active: boolean;
    translations: [
        {
            language_id: number;
            name: string;
        },
        {
            language_id: number;
            name: string;
        }
    ];
};

export type specificationDataType = {
    id: number;
    absolute_name: string;
    translatons: [
        {
            language_id: number;
            name: string;
        }
    ];
    enabled_values: [
        {
            id: number;
            absolute_value: string;
            enable: boolean;
            value_translations: null | {
                language_id: number;
                name: string;
            }
        }
    ],
    disabled_values: [
        {
            id: number;
            absolute_value: string;
            enable: boolean;
            value_translations: null | {
                language_id: number;
                name: string;
            }
        }
    ]
}

export const SpecificationsActions =  {
    changeIsLoading: (bool: boolean) => ({type: 'CHANGE_IS_LOADING', bool} as const),
    changeSpecificationActivation: (id: number, boolValue: boolean) => ({type: 'CHANGE_SPECIFICATION_ACTIVATION', data: {id, boolValue}} as const),
    changeSearchedSpecificationActivation: (id: number, boolValue: boolean) => ({type: 'CHANGE_SEARCHED_SPECIFICATION_ACTIVATION', data: {id, boolValue}} as const),
    addSpecifications: (specifications: Array<specificationsDataType>) => ({type: 'ADD_SPECIFICATIONS', specifications} as const),
    addSearchedSpecifications: (specifications: Array<specificationsDataType>) => ({type: 'ADD_SEARCHED_SPECIFICATIONS', specifications} as const),
    makeSearchedSpecificationsNull: () => ({type: 'MAKE_SEARCHED_SPECIFICATIONS_NULL'} as const),
    addSpecification: (specification: specificationDataType) => ({type: 'ADD_SPECIFICATION', specification} as const),
    switchEnabledToDisabledSpecification: (value: any) => ({type: 'SWITCH_ENABLE_TO_DISABLE_SPECIFICATION', value} as const),
    switchDisabledToEnabledSpecification: (value: any) => ({type: 'SWITCH_DISABLE_TO_ENABLE_SPECIFICATION', value} as const),
};


export const SpecificationsReducer = (state = SpecificationsInitialState, action: ActionsType ) => {
    switch (action.type) {
        case 'CHANGE_SEARCHED_SPECIFICATION_ACTIVATION':
            return {
                ...state,
                searchSpecifications: [...state.searchSpecifications.map((specification: any) => {
                    if (specification.id === action.data.id) {
                        specification.is_active = action.data.boolValue;
                    }
                    return specification;
                })]
            };
        case 'CHANGE_SPECIFICATION_ACTIVATION':
            return {
                ...state,
                specificationsData: [...state.specificationsData.map((specification) => {
                    if (specification.id === action.data.id) {
                        specification.is_active = action.data.boolValue;
                    }
                    return specification;
                })]
            };
        case 'CHANGE_IS_LOADING':
            return {
                ...state,
                isLoading: action.bool
            };
        case 'ADD_SPECIFICATIONS':
            return {
                ...state,
                specificationsData: action.specifications
            };
        case 'ADD_SEARCHED_SPECIFICATIONS':
            debugger;
                return {
                    ...state,
                    searchSpecifications: action.specifications
                };
        case 'MAKE_SEARCHED_SPECIFICATIONS_NULL':
            debugger;
            return {
                ...state,
                searchSpecifications: null
            };
        case 'ADD_SPECIFICATION':
            return {
                ...state,
                specificationData: action.specification,
                enabled_values: action.specification.enabled_values,
                disabled_values: action.specification.disabled_values,
            };
        case 'SWITCH_ENABLE_TO_DISABLE_SPECIFICATION':
            debugger;
            return {
                ...state,
                enabled_values: state.enabled_values.filter((value: any) => value.id !== action.value.id),
                disabled_values: [...state.disabled_values, action.value]
            };
        case 'SWITCH_DISABLE_TO_ENABLE_SPECIFICATION':
            debugger;
            return {
                ...state,
                disabled_values: state.disabled_values.filter((value: any) => value.id !== action.value.id),
                enabled_values: [...state.enabled_values, action.value]
            };
        default:
            return state;
    }
};


// Store type
export type SpecificationsInitialStateType = typeof SpecificationsInitialState;

// Action types
export type ActionsType = InferActionsTypes<typeof SpecificationsActions>;