import { Link } from 'react-router-dom';
import styles from './Sidebar.module.css';
import {useHistory} from 'react-router';
import { useSelector } from 'react-redux';
import { GetRoleId } from '../../Redux/Auth/AuthSelector';

export const Sidebar = () => {
    const history = useHistory();
    const location = history.location.pathname;
    const role_id = useSelector(GetRoleId);

    return (
        <div className={styles.main_block}>
            {(role_id === 1) 
            ?
                <>
                    <Link to='/operators' className={`${(location === '/operators') ? 'bg-yellow-400' : ''} ${styles.links_initial}`}>Operatorlar</Link>
                    <Link to='/specifications' className={`${(location === '/specifications') ? 'bg-yellow-400' : ''} ${styles.links_initial}`}>Spesifikasiýalar</Link>
                    <Link to='/types' className={`${(location === '/types') ? 'bg-yellow-400' : ''} ${styles.links_initial}`}>Görnüşler</Link>
                    <Link to='/locations' className={`${(location === '/locations') ? 'bg-yellow-400' : ''} ${styles.links_initial}`}>Locations</Link>
                    <Link to='/pictures' className={`${(location === '/pictures') ? 'bg-yellow-400' : ''} ${styles.links_initial}`}>Suratlar</Link>
                </>
            :   
                <>
                    <Link className={`${(location === '/unapproved') ? 'bg-yellow-400' : ''} ${styles.links_initial}`} to='/unapproved'>Garaşylýanlar</Link>
                    <Link className={`${(location === '/vip') ? 'bg-yellow-400' : ''} ${styles.links_initial}`} to='/vip'>VIP-lar</Link>
                </>
            }
            
        </div>
    );
};