import { api } from '../../../api';
import { LocationsActions } from './LocationsReducer';


export const getMainLocationsThunk = (setIs_loading: (boolValue: boolean) => void, token: string) => {
    return async (dispatch: any) => {
        try {
            if (token === "") return;
            const response: any = await api.getApi({url: `admin/main-locations`, token: token, withCredentials: true, contentType: false});
            dispatch(LocationsActions.addMainLocations(response.data.rows));
            setIs_loading(false);
        } catch (error: any) {
            console.log(error.response.message);
        }
    };
};

export const getRegionLocationsThunk = (id: number, token: string) => {
    return async (dispatch: any) => {
        try {
            debugger;
            const response = await api.getApi({url: `admin/region-locations/${id}`, token: token, withCredentials: true, contentType: false});                
            debugger;
            dispatch(LocationsActions.addRegionLocations(id, response.data.rows));
        } catch (error: any) {
            console.log(error.response.message);
        }
    };
};

export const addRegionLocationsThunk = (data: any, token: string) => {
    return async (dispatch: any) => {
        try {
            debugger;
            const response = await api.postApi({url: `admin/add-location`, token: token, withCredentials: true, params: data});                
            debugger;
            dispatch(LocationsActions.addNewRegionLocations(response.data.rows));
  
        } catch (error: any) {
            console.log(error.response.message);
        }
    };
};


