import { FormProvider, useForm } from 'react-hook-form';
import Modal from 'react-modal';
import { useDispatch, useSelector } from 'react-redux';
import { addOperatorThunk } from '../../../Redux/Admin/Operators/OperatorsThunk';
import { GetAccessToken } from '../../../Redux/Auth/AuthSelector';
import { EmailInput } from '../../Inputs/EmailInput/EmailInput';
import { NameInput } from '../../Inputs/NameInput/NameInput';
import { PasswordInput } from '../../Inputs/PasswordInput/PasswordInput';
import { PhoneInput } from '../../Inputs/PhoneInput/PhoneInput';
import styles from './AddOperatorModal.module.css';

type propsType = {
    isModalShown: boolean;
    setIsModalShown: (value: boolean) => void;  
};



export const AddOperatorModal = (props: propsType) => {
    const dispatch = useDispatch();
    const access_token = useSelector(GetAccessToken);

    const onFormSubmit = (data: any) => {
        dispatch(addOperatorThunk({name: data.name, phone: data.phone, email: data.email, password: data.password}, access_token));
        props.setIsModalShown(false);
    }


    const methods = useForm<{ name: string; phone: string; password: string; }>();
    const { handleSubmit } = methods;
    return (
        <Modal className={styles.main_block} isOpen={props.isModalShown}>
            <FormProvider {...methods}>
                <div className={styles.content_block}>
                    <form onSubmit={handleSubmit(onFormSubmit)}>
                        <h1 className={styles.title}>Operator goşmak</h1>
                        <NameInput />
                        <PhoneInput />
                        <EmailInput />
                        <PasswordInput />
                        <button className={styles.add_button}>Goşmak</button>
                    </form>
                </div>
            </FormProvider>
        </Modal>
    );
};