// Components
import { Locations } from "./Pages/Admin/Locations/Locations";
import { Operators } from "./Pages/Admin/Operators/Operators";
import { Pictures } from "./Pages/Admin/Images/Images";
import { Specifications } from "./Pages/Admin/Specifications/Specifications";
import { Types } from "./Pages/Admin/Types/Types";
import { Unapproved } from "./Pages/Operator/Unapproved/Unapproved";
import { Vip } from "./Pages/Operator/VIP/Vip";


// Admin paths
const operators = '/operators';
const specifications = '/specifications';
const types = '/types';
const locations = '/locations';
const pictures = '/pictures';


// Operator paths
const unapproved = '/unapproved';
const vip = '/vip';


export const AdminRoutes = [
    {
        path: operators,
        Component: Operators,
    },
    {
        path: specifications,
        Component: Specifications
    },
    {
        path: types,
        Component: Types
    },
    {
        path: locations,
        Component: Locations
    },
    {
        path: pictures,
        Component: Pictures
    }
];

export const OperatorRoutes = [
    {
        path: unapproved,
        Component: Unapproved
    },
    {
        path: vip,
        Component: Vip
    },
];
