import { InferActionsTypes } from '../../store';


const LocatiosInitialState = {
    mainLocations: [],
    regionLocations: []
};


export const LocationsActions =  {
    addMainLocations: (mainLocations: {id: number; name: string}) => ({type: 'ADD_MAIN_LOCATION', mainLocations} as const),
    addNewRegionLocations: (regionLocations: {id: number; name: string}) => ({type: 'ADD_NEW_REGION_LOCATION', regionLocations} as const),
    addNewMainLocations: (mainLocations: {id: number; name: string}) => ({type: 'ADD_NEW_MAIN_LOCATION', mainLocations} as const),
    addRegionLocations: (id: number, regionLocations: {id: number; name: string}) => ({type: 'ADD_REGION_LOCATION', data: {id, regionLocations}} as const),
};


export const LocationsReducer = (state = LocatiosInitialState, action: ActionsType ) => {
    switch (action.type) {
        case 'ADD_MAIN_LOCATION':
            return {
                ...state,
                mainLocations: action.mainLocations
            };
        case 'ADD_REGION_LOCATION':
            debugger;
            return {
                ...state,
                regionLocations: action.data.regionLocations
            };
        case 'ADD_NEW_REGION_LOCATION':
            debugger;
            return {
                ...state,
                regionLocations: [...state.regionLocations, action.regionLocations]
            }
        case 'ADD_NEW_MAIN_LOCATION':
            debugger;
            return {
                ...state,
                mainLocations: [...state.mainLocations, action.mainLocations]
            }
        default:
            return state;
    }
};


// Store type
export type OperatorsInitialStateType = typeof LocatiosInitialState;

// Action types
export type ActionsType = InferActionsTypes<typeof LocationsActions>;