import { InferActionsTypes } from '../../store';


const OperatorsInitialState = {
    operators: [],
    operatorsCount: null,
    searchedOperators: null,
    isLoading: false
};

export type operatorsDataType = {
    full_name: string;
    email: string;
    phone: string;
    id: string;
};


export const OperatorsActions =  {
    changeIsLoading: (bool: boolean) => ({type: 'CHANGE_IS_LOADING', bool} as const),
    addOperators: (operators: Array<operatorsDataType>) => ({type: 'ADD_OPERATORS', operators} as const),
    addSerchedOperators: (operators: Array<operatorsDataType>) => ({type: 'ADD_SEARCHED_OPERATORS', operators} as const),
    changeOperatorsCound: (count: number) => ({type: 'CHANGE_OPERATORS_COUNT', count} as const),
    makeSearchOperatorsNull: () => ({type: 'MAKE_SEARCH_OPERATORS_NULL'} as const) 
};


export const OperatorsReducer = (state = OperatorsInitialState, action: ActionsType ) => {
    switch (action.type) {
        case 'CHANGE_IS_LOADING':
            return {
                ...state,
                isLoading: action.bool
            };
        case 'ADD_OPERATORS':
            return {
                ...state,
                operators: action.operators
            };
        case 'CHANGE_OPERATORS_COUNT':
            return {
                ...state,
                operatorsCount: action.count
            };
        case 'ADD_SEARCHED_OPERATORS':
            return {
                ...state,
                searchedOperators: action.operators
            };
        case 'MAKE_SEARCH_OPERATORS_NULL':
            return {
                ...state,
                searchedOperators: null
            };
        default:
            return state;
    }
};


// Store type
export type OperatorsInitialStateType = typeof OperatorsInitialState;

// Action types
export type ActionsType = InferActionsTypes<typeof OperatorsActions>;