import Modal from 'react-modal';
import { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
// Components
import { GetAccessToken } from '../../../Redux/Auth/AuthSelector';
import { Slider } from '../../Slider/Slider';
// State
import { getEstateDataThunk, sendEstateActivationThunk } from '../../../Redux/Operator/Unapproved/UnapprovedThunk';
// Images
import x from '../../../Images/x.png';
// Styles
import styles from './EstateActivationModal.module.css';


export const EstateActivationModal = (props: {id: number, changeModalOpen: (bool: boolean) => void, isModalOpen: boolean}) => {
     
    const dispatch = useDispatch();
    const access_token = useSelector(GetAccessToken);

    const onButtonClick = (booleanValue: boolean) => {
        dispatch(sendEstateActivationThunk(props.id, booleanValue, access_token));
        props.changeModalOpen(false);
    };

    useEffect(() => {
        dispatch(getEstateDataThunk(access_token, props.id));
    }, [access_token, dispatch, props.id]);


    return (
        <Modal className={styles.main_block} isOpen={props.isModalOpen}>
            <div className={styles.content_block}>
                <img alt='X' src={x} onClick={() => props.changeModalOpen(false)} className={styles.exit_image}/> 
                <Slider />
                <div className={styles.button_block}>
                    <button className={styles.enable_button} onClick={() => onButtonClick(true)}>Rugsat bermek</button>
                    <button className={styles.disable_button} onClick={() => onButtonClick(false)}>Yzyna gaýtarmak</button>
                </div>
            </div>
        </Modal>
    );
}