import { useState } from 'react';
import { useForm } from 'react-hook-form';
import Modal from 'react-modal';
import { useDispatch, useSelector } from 'react-redux';
import { createSpecificationThunk } from '../../../Redux/Admin/Specifications/SpecificatiosThunk';
import { GetAccessToken } from '../../../Redux/Auth/AuthSelector';
import x from '../../../Images/x.png';
import styles from './AddSpecificationModal.module.css';

export const AddSpecificationModal = (props: {changeModalOpen: (bool: boolean) => void, isModalOpen: boolean}) => {
    const [absolute_values_count, setAbsolute_values_count]: any = useState([]);
    const dispatch = useDispatch();
    const addAbsoluteValue = (data: any) => {
        setAbsolute_values_count([...absolute_values_count, {title: data.target.value, id: Date.now()}]);
    }

    const removeAbsoluteValue = (id: number) => {
        setAbsolute_values_count(absolute_values_count.filter((index: any) => index.id !== id));
    }
    const { register, handleSubmit } = useForm();
    let a: any = [];
    let b: any = [];
    const access_token = useSelector(GetAccessToken);
    const onFormSubmit = (data: any) => {
        const absoluteValuesCount = absolute_values_count.length;
        console.log(absoluteValuesCount);
        
        for (let i = 0; i <= absoluteValuesCount; i++) {
            a.push([{lang_id: 1, name: data[`absolute_value_turkmen_${i}`]}, {lang_id: 2, name: data[`absolute_value_russian_${i}`]}]);
            b.push(data[`absolute_value_${i}`]);
        }
        const preparedData = {
            absolute_name: data.absolute_name,
            is_required: data.absolute_name_isRequired,
            is_multiple: data.absolute_name_isMultiple,
            translations: [
                {lang_id: 1, name: data.absolute_name_turkmen},
                {lang_id: 2, name: data.absolute_name_russian},
            ],
            absolute_values: b,
            values_translations: a,
        };
        dispatch(createSpecificationThunk(preparedData, access_token));
    }

    return (
        <Modal isOpen={props.isModalOpen} className={styles.main_block}>
            <div className={`${styles.content_block} relative top-0 left-0`}>
                <img alt='X' src={x} onClick={() => props.changeModalOpen(false)} className='absolute w-4 h-4 top-2 right-2 cursor-pointer'/>
                <h1 className='text-center'>Täze spesifikasiýa goşmak</h1>
                <div className={styles.absolute_name_block}>
                    <input 
                        className={styles.absolute_name} 
                        placeholder='Absolut ady' 
                        {...register('absolute_name', {required: true})}
                    />
                    <div className={styles.absolute_name_translation}> 
                        <input className={styles.absolute_name_turkmen} 
                            placeholder='Türkmençe ady'
                            type='text'
                            {...register('absolute_name_turkmen', {required: true})}
                        />
                        <input 
                            className={styles.absolute_name_russian}
                            placeholder='Rusça ady' 
                            {...register('absolute_name_russian', {required: true})}
                        />
                    </div>
                    <div className={styles.absolute_name_checkbox_block}>
                        <div> 
                            <input 
                                id='isRequired' 
                                type='checkbox'
                                {...register('absolute_name_isRequired')}
                            />
                            <label htmlFor='isRequired'>Hökmanmy</label>
                        </div>
                        <div>
                            <input 
                                id='isMusliple' 
                                type='checkbox'
                                {...register('absolute_name_isMultiple')}
                            />
                            <label htmlFor='isMultiple'>Köpmi</label>
                        </div>
                    </div>
                </div>
                <div className={styles.add_absolute_value_block}>
                    <input 
                        className={styles.absolute_value} 
                        placeholder='Absolut manysy'
                        {...register('absolute_value_0', {required: true})}
                    />
                    <input 
                        placeholder='Türkmençe'
                        {...register('absolute_value_turkmen_0')}
                    />
                    <input 
                        placeholder='Rusça'
                        {...register('absolute_value_russian_0')}
                    />
                    <button className={styles.add_absolute_value_button} onClick={addAbsoluteValue}> Goşmak</button>
                </div>
                {absolute_values_count.map((input: any, index: number) => {
                    return (
                        <div className={styles.add_absolute_value_block}>
                            <input 
                                className={styles.add_absolute_value_input} 
                                placeholder='Absolut many goşmak'
                                {...register(`absolute_value_${index+1}`, {required: true})}
                            />
                            <input 
                                className={styles.add_absolute_value_input} 
                                placeholder='Türkmençe'
                                {...register(`absolute_value_turkmen_${index+1}`)}
                            />
                            <input 
                                className={styles.add_absolute_value_input} 
                                placeholder='Rusça'
                                {...register(`absolute_value_russian_${index+1}`)}
                            />
                            <button className={styles.remove_absolute_value_button} onClick={() => removeAbsoluteValue(input.id)}>Pozmak</button>
                        </div>
                    )
                })}
                <button type='submit' onClick={handleSubmit(onFormSubmit)} className={styles.create_button}>Döretmek</button>
            </div>

        </Modal>

    )
}